@import url(https://fonts.googleapis.com/css?family=Gurajada&subset=latin,telugu);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
    margin: 0;
    background: rgb(221, 218, 218);
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

ul{
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    background: black;
    padding: 2%;
}
li{
    background: black;
    justify-content: stretch;
    list-style-type: none;
}
a{
    display: inline-block;
    min-width: 5em;
    text-align: center;
    background: grey;
    color: black;
    padding: 10px;
    font-size: 16px;
    border: none;
    border-radius: 11px;
    text-decoration: none;
    height: -webkit-max-content;
    height: max-content;
}
a:hover{
    background: crimson;
}
.dropdown {
    display: none;
    position: fixed;
    border-radius: 11px;
}
.dropdown a {
    color: black;
    padding: 12px 16px;
    text-decoration: none;
    display: block;
}
li:hover > ul.dropdown {
    display: block;
}



footer{
    position: fixed;
    bottom: 0;
    margin: 0px 0px 3px 17px;
    color: crimson;
}
footer{
    position: fixed;
    bottom: 0px;
    margin-left: 7px;
    color: crimson;
}
.container{
    margin: 7vh auto;
    padding: 21px;
    width: 40%;
    border-radius: 6px;
    box-shadow: 10px 10px 16px 0 black;
    display: flex;
    align-items: center;
    justify-content: center;
    background: grey;
}
form{
    background: grey;
    text-align: center;
} 
label{
    display: inline-block;
    background: grey;
    padding: 10px;
}
input{
    background: rgb(221, 218, 218);
}
textarea{
    margin: 10px;
    background: rgb(221, 218, 218);
}
.link{
    display: inline-block;
    width: 5em;
    text-align: center;
    background: black;
    color: rgb(221, 218, 218);    
    padding: 10px;
    font-size: 16px;
    border: none;
    border-radius: 11px;
    text-decoration: none;
}
.link:hover{
    background: crimson;
    color: black;
}
.thanks{
    color: crimson;
    background: grey;
}


